import React, { useState } from 'react';
// Yandex Map
import { YMaps, Map, Placemark } from 'react-yandex-maps';
// Components
import IconSkeleton from '../../../Common/Icon/IconSkeleton';
// Styles-module
import yandexMap from './yandexMap.module.scss';
// Image
import placemarkMap from '../../../../assets/image/placemark.svg';

const YandexMap: React.FC = () => {
  // React State
  const [mapLoad, setMapLoad] = useState<boolean>(true);

  return (
    <YMaps>
      {mapLoad && (
        <IconSkeleton
          cssClasses={{
            backgroundColor: 'active-stopColor-2',
            loaderColor: 'active-stopColor-1',
          }}
        />
      )}
      <Map
        onLoad={() => setMapLoad(false)}
        defaultState={{
          center: [53.897491757579516, 27.569602765625],
          zoom: 5,
        }}
        className={yandexMap.main}
      >
        <Placemark
          options={{
            iconLayout: 'default#image',
            iconImageHref: placemarkMap,
            iconImageSize: [40, 40],
            iconImageOffset: [-20, -34],
          }}
          geometry={[52.10841813121317, 23.702415265625]}
        />
      </Map>
    </YMaps>
  );
};

export default YandexMap;
